'use client'; // Necessário para usar estados (useState) no Next.js

import { useState, useEffect } from 'react';

// Tipagem do Serviço
interface Service {
  id: string;
  name: string;
  price: string;
  duration_minutes: number;
}

export default function AdminPage() {
  const [services, setServices] = useState<Service[]>([]);
  const [formData, setFormData] = useState({
    name: '',
    price: '',
    duration_minutes: '',
    description: ''
  });

  // Carregar serviços ao abrir a página
  useEffect(() => {
    fetchServices();
  }, []);

  async function fetchServices() {
    const res = await fetch('http://localhost:3001/services');
    const data = await res.json();
    setServices(data);
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    // Enviar para o Backend
    await fetch('http://localhost:3001/services', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(formData),
    });
    
    // Limpar formulário e recarregar lista
    setFormData({ name: '', price: '', duration_minutes: '', description: '' });
    fetchServices();
    alert('Serviço cadastrado com sucesso!');
  }

  async function handleDelete(id: string) {
    if(!confirm("Tem certeza que deseja excluir?")) return;
    
    await fetch(`http://localhost:3001/services/${id}`, { method: 'DELETE' });
    fetchServices();
  }

  return (
    <div className="min-h-screen bg-gray-100 p-8 font-sans text-gray-800">
      <div className="max-w-4xl mx-auto">
        <h1 className="text-3xl font-bold mb-8 text-amber-700">Painel Administrativo - Serviços</h1>

        {/* Formulário de Cadastro */}
        <div className="bg-white p-6 rounded-lg shadow-md mb-8">
          <h2 className="text-xl font-semibold mb-4">Cadastrar Nova Trança</h2>
          <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-4">
            
            <input 
              type="text" 
              placeholder="Nome do Modelo (ex: Box Braids)" 
              className="border p-2 rounded"
              value={formData.name}
              onChange={(e) => setFormData({...formData, name: e.target.value})}
              required
            />
            
            <input 
              type="number" 
              placeholder="Preço (R$)" 
              className="border p-2 rounded"
              value={formData.price}
              onChange={(e) => setFormData({...formData, price: e.target.value})}
              required
            />

            <input 
              type="number" 
              placeholder="Duração (minutos)" 
              className="border p-2 rounded"
              value={formData.duration_minutes}
              onChange={(e) => setFormData({...formData, duration_minutes: e.target.value})}
              required
            />

            <input 
              type="text" 
              placeholder="Descrição curta" 
              className="border p-2 rounded"
              value={formData.description}
              onChange={(e) => setFormData({...formData, description: e.target.value})}
            />

            <button type="submit" className="md:col-span-2 bg-green-600 text-white p-3 rounded font-bold hover:bg-green-700 transition">
              + Salvar Serviço
            </button>
          </form>
        </div>

        {/* Lista de Serviços */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-semibold mb-4">Serviços Ativos</h2>
          <ul>
            {services.map((service) => (
              <li key={service.id} className="flex justify-between items-center border-b py-3">
                <div>
                  <strong className="text-lg">{service.name}</strong>
                  <p className="text-gray-600">R$ {service.price} • {service.duration_minutes} min</p>
                </div>
                <button 
                  onClick={() => handleDelete(service.id)}
                  className="text-red-500 hover:text-red-700 font-semibold"
                >
                  Excluir
                </button>
              </li>
            ))}
            {services.length === 0 && <p className="text-gray-500">Nenhum serviço cadastrado.</p>}
          </ul>
        </div>

      </div>
    </div>
  );
}